<template>
    <div>
        <h1> Member List </h1>
        <table>
            <thead>
                <th> Member Id </th>
                <th> Member name </th>
                <th> Member Age </th>
                <th> Member Email </th>
            </thead>
            <tbody>
                <tr v-for="product in products" v-bind:key="product.id">
                    <td> {{product.id}} </td>
                    <td> {{product.name}} </td>
                    <td> {{product.brand}} </td>
                    <td> {{product.madein}} </td>
                    <td> {{product.price}} </td>
                </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
import MemberService from '../services/MemberService';

    export default {
        name: 'Members-vue',
        data() {
            return {
                members : []
            }
            
        },
        methods: {
            getMembers() {
                MemberService.getMembers().then((response) => {
                    this.members = response.data;
                });
            }
        },
        created() {
            this.getMembers();
        }
    }
</script>